import { instantiate } from 'cc';
import { Vec3 } from 'cc';
import { ccenum } from 'cc';
import { CCFloat } from 'cc';
import { math } from 'cc';
import { tween } from 'cc';
import { Node } from 'cc';
import { _decorator, Component, Prefab } from 'cc';

ccenum(CCFloat);
const { ccclass, property } = _decorator;
@ccclass('itemParticle')
export class itemParticle extends Component {
    @property({ type: Prefab, tooltip: "" })
    public pb: Prefab = null!;
    @property({ type: CCFloat, tooltip: "" })
    public maxNum: number = 0;
    @property({ type: CCFloat, tooltip: "" })
    public createNum: number = 0;
    @property({ type: CCFloat, tooltip: "" })
    public disTime: number = 0;
    @property({ type: CCFloat, tooltip: "" })
    public startScale: number = 0;
    @property({ type: CCFloat, tooltip: "" })
    public endScale: number = 0;
    @property({ type: CCFloat, tooltip: "" })
    public flyTime: number = 0;
    @property({ type: CCFloat, tooltip: "w" })
    public wRange: number = 0;
    @property({ type: CCFloat, tooltip: "h" })
    public hRange: number = 0;

    onLoad() {

    }

    start() {
    }

    private createTime: number = 0;
    update() {
        if (this.node.active == false) {
            this.init();
            return;
        }
        let curTime: number = new Date().getTime();
        if (this.createTime) {
            let dis: number = curTime - this.createTime;
            if (dis < this.disTime * 1000) {
                return;
            }
        }
        this.create();
        this.createTime = curTime;
    }

    private init() {
        this.createTime = 0;
        for (let i: number = 0; i < this.node.children.length; i++) {
            let item: Node = this.node.children[i];
            this.recycle(item);
        }
    }

    private pool: Node[] = [];
    private create() {
        let num:number = this.createNum*math.random();
        for (let i: number = 0; i < this.createNum; i++) {
            this.createOne();
        }
    }

    private createOne() {
        if (this.node.children.length >= this.maxNum) {
            return;
        }
        let item: Node = this.factory();
        item.scale = new Vec3(this.startScale, this.startScale);
        item.x=0;
        item.y=0;
        item.angle = math.random()*360;
        let r: number = 360 * math.random();
        let aimPos: Vec3 = new Vec3(this.wRange*Math.cos(r), this.hRange*Math.sin(r))
        let delay:number=math.random();
        tween(item)
            .delay(delay)
            .call(()=>{
                this.node.addChild(item);
            })
            .to(this.flyTime, { scale: new Vec3(this.endScale, this.endScale), position: aimPos })
            .call(() => {
                this.recycle(item);
            })
            .start();
    }

    private factory(): Node {
        if (this.pool.length > 0) {
            return this.pool.shift() as Node;
        } else {
            return instantiate(this.pb);
        }
    }

    private recycle(item: Node) {
        item.parent = null;
        this.pool.push(item);
    }
}