import { _decorator, Component, Node } from 'cc';
import { songkran } from './cSongkran';
import { Label } from 'cc';
import { songkranSymbol } from './songkranSymbol';
import { Button } from 'cc';
import { ESHOWTYPE } from '../../module_basic/ui_slot/match3/baseSymbol';
const { ccclass, property } = _decorator;

const GAME_COMBINATIONS: any[] = [
    [],
    [1, 3, 5],
    [1, 3, 5],
    [1, 3, 5],
    [1, 3, 5],
    [1, 3, 5],
    [1, 3, 5],
    [5, 10, 30],
    [5, 10, 30],
    [10, 20, 50],
    [10, 20, 50],
    [30, 50, 100],
    [50, 100, 200]
];

@ccclass('songkranDetail')
export class songkranDetail extends Component {
    @property({ type: Node, tooltip: "" })
    public BGNode: Node = null!;
    @property({ type: songkranSymbol, tooltip: "" })
    public symbol: songkranSymbol = null!;
    @property({ type: Node, tooltip: "nums" })
    public numNode: Node = null!;
    @property({ type: Label, tooltip: "" })
    public num1: Label = null!;
    @property({ type: Label, tooltip: "" })
    public num2: Label = null!;
    @property({ type: Label, tooltip: "" })
    public num3: Label = null!;
    @property({ type: Node, tooltip: "specialNode" })
    public specialNode: Node = null!;

    init(symbol: songkranSymbol) {
        let symbolId: number = symbol.symbolId;
        let reelIndex: number = symbol.reelDate.index;
        let idx: number = symbolId - 1;
        this.symbol.getComponent(Button).enabled = false;
        this.symbol.symbolId = symbolId;
        this.symbol.showType = ESHOWTYPE.ske;
        this.symbol.spawn();
        let config = GAME_COMBINATIONS[idx];
        if (config) {
            this.num1.string = config[0];
            this.num2.string = config[1];
            this.num3.string = config[2];
        }
        if (symbolId < 13) {
            this.numNode.active = true;
            this.specialNode.active = false;
            this.BGNode.w = 2 * 150;
        } else {
            this.numNode.active = false;
            this.specialNode.active = true;
            this.BGNode.w = 3 * 150;
            this.specialNode.children[0].active = symbolId == 13;
            this.specialNode.children[1].active = symbolId == 14;
        }
        if (reelIndex <= 2) {
            this.BGNode.scale_x = 1;
            this.BGNode.x = -80;
            this.numNode.x = 150 ;
            this.specialNode.x = 220;
        } else {
            this.BGNode.scale_x = -1;
            this.BGNode.x = 80;
            this.numNode.x = -130;
            this.specialNode.x = -220;
        }
    }

    onclick() {
        songkran.main.detailMask.active = false;
        this.node.destroy();
        songkran.detail = null;
        songkran.main.detailMask.active = false;
    }

}