import { Node } from "cc";
import { math } from "cc";
import { Animation } from "cc";
import { instantiate } from "cc";
import { UITransform } from "cc";
import { Vec3 } from "cc";
import { Label } from "cc";
import { Tween } from "cc";
import { tween } from "cc";
import { Component } from "cc";
import { _decorator } from "cc";
import { songkran } from "./cSongkran";
import { EBGType } from "./songkranBg";
import { songkranDetail } from "./songkranDetail";
import { EventTouch } from "cc";
import { Button } from "cc";


const { ccclass, property } = _decorator;

@ccclass('songkranFreeBegin')
export class songkranFreeBegin extends Component {
    @property({ type: Node, tooltip: "" })
    public bg: Node = null!;
    @property({ type: Label, tooltip: "" })
    public times: Label = null!;
    @property({ type: Node, tooltip: "" })
    public light: Node = null!;
    @property({ type: Node, tooltip: "" })
    public items: Node = null!;
    @property({ type: Node, tooltip: "" })
    public clock: Node = null!;
    @property({ type: Node, tooltip: "" })
    public btn: Node = null!;
    @property({ type: Node, tooltip: "" })
    public girl: Node = null!;

    onLoad() {

    }

    protected start(): void {
        this.freeShow(songkran.rollData.free);
    }

    freeShow(freeTimes: number) {
        this.playOverBn = false;
        this.node.active = true;
        this.times.string = freeTimes + "";
        this.bg.active = false;
        this.items.active = false;
        this.light.active = true;
        this.items.opacity = 100;
        this.girl.scale = new Vec3(0.5, 0.5);
        this.girl.y = -50;
        this.clock.children[0].angle = 0;
        (this.clock.children[1].getComponent(Label) as Label).string = "0%";
        this.btn.scale = new Vec3(0, 0);
        this.btn.opacity = 0;
        tween(this.node)
            .delay(0.02)
            .call(() => {
                this.items.active = true;
                tween(this.items)
                    .to(0.5, { opacity: 255 })
                    .start();
                tween(this.girl)
                    .to(0.98, { y: 0, scale: new Vec3(1, 1) })
                    .start();
            })
            .delay(0.1)
            .call(() => {
                this.bg.active = true;
                songkran.main.bg.bgState = EBGType.free;
            })
            .delay(0.88)
            .call(() => {
                this.showBtn();
            })
            .start();
    }

    private showBtn() {
        let round: Node = this.clock.children[0] as Node;
        this.btn.opacity = 255;

        tween(round)
            .to(0.5, { angle: -90 })
            .call(() => {
                tween(this.btn)
                    .to(0.5, { scale: new Vec3(1.2, 1.2) })
                    .to(0.1, { scale: new Vec3(1, 1) })
                    .call(() => {
                        this.playOverBn = true;
                        this.scheduleOnce(this.freeHide, 5)
                        tween(this.girl)
                            .to(5, { scale: new Vec3(1.1, 1.1) })
                            .start();
                    })
                    .start();
            })
            .start();
        this.scheduleOnce(() => {
            (this.clock.children[1].getComponent(Label) as Label).string = "100%";
        }, 0.4);
    }

    private playOverBn: boolean = false;
    freeHide() {
        if (this.playOverBn) {
            this.girl.scale = new Vec3(0.5, 0.5);
            this.girl.y = -50;
            this.node.destroy();
            songkran.freeBegin = undefined;
            songkran.main.freeBeginHide();
        }

    }


}
