import { _decorator, Component, Node } from 'cc';
import { UserMgr } from '../../module_basic/scripts/UserMgr';
import { SceneDef } from '../../scripts/SceneDef';
import { lobbyNet } from '../../module_basic/scripts/NetGameServer';
import { UIChat } from '../../module_basic/ui_chat/UIChat';
import { UIAnnouncement } from '../../module_basic/ui_announcement/UIAnnouncement';
import { SlotsSongkranGameMgr } from './SlotsSongkranGameMgr';
import { SubGameMgr } from '../../module_basic/scripts/SubGameMgr';
import { SubGameDef } from '../../module_basic/scripts/SubGameDef';



const { ccclass, property } = _decorator;

@ccclass('SlotsSongkranLobbyScene')
export class SlotsSongkranLobbyScene extends Component {
    @property(Node) announcementPlacer: Node;

    async start() {
        if (!UserMgr.inst.uid) {
            tgx.SceneUtil.loadScene(SceneDef.START);
            return;
        }

        tgx.UIMgr.inst.closeAll();

        // if (lobbyNet.type != 'http') {
        //     tgx.UIMgr.inst.showUI(UIChat, null, null, lobbyNet);
        // }

        tgx.UIMgr.inst.showUI(UIAnnouncement, (ui: UIAnnouncement) => {
            ui.setPosition(this.announcementPlacer.position.x, this.announcementPlacer.position.y);
        });
    }


    update(deltaTime: number) {

    }

    onBtnSoloModeClicked() {
        SlotsSongkranGameMgr.inst.initSoloMode();
        SubGameMgr.inst.CreateRoom(SubGameDef.SLOTS_SONGKRAN, true);
    }
}


