import { Node } from "cc";
import { math } from "cc";
import { Animation } from "cc";
import { Tween } from "cc";
import { tween } from "cc";
import { Component } from "cc";
import { _decorator } from "cc";

import { macro } from "cc";

import { songkran } from "./cSongkran";
import { Label } from "cc";
import { Vec3 } from "cc";
import { Num } from "../../core_tgx/framework/util/Num";
import { lampUI } from "../../module_basic/utils/lampUI";


const { ccclass, property } = _decorator;

@ccclass('infoBoard')
export class infoBoard extends Component {
    @property({ type: [Node] })
    public bgs: Node[] = [];
    @property({ type: [Node] })
    public bgEffects: Node[] = [];
    @property({ type: [Animation], tooltip: "sprays" })
    public sprays: Animation[] = [];
    @property({ type: Animation, tooltip: "light" })
    public light: Animation = null!;
    @property({ type: Animation, tooltip: "star" })
    public star: Animation = null!;
    @property({ type: Node, tooltip: "infoText" })
    public infoText: Node = null!;
    @property({ type: Node, tooltip: "infoWin" })
    public infoWin: Node = null!;


    private _showInfoTextBn: boolean = true;
    public get showInfoTextBn(): boolean {
        return this._showInfoTextBn;
    }
    public set showInfoTextBn(v: boolean) {
        this._showInfoTextBn = v;
        this.infoText.active = v;
        this.infoWin.active = !this.infoText.active;
        if (this.infoText.active) {
            this.winLevel = EWinLev.one;
        }
        if (this.infoText.active) {
            this.hideBgEffect();
        }
    }

    private _infoTextType: number = -1;
    public get infoTextType(): ETextType {
        return this._infoTextType;
    }
    /** */
    public set infoTextType(v: ETextType) {
        if (this._infoTextType == v) return;
        this._infoTextType = v;
        let arr: number[] = [];
        switch (this.infoTextType) {
            case ETextType.normal:
                arr = [4, 5, 6]
                break;
            case ETextType.free:
                arr = [0, 1, 2, 3, 5, 6]
                break;
            case ETextType.oneMore:
                arr = [0, 1, 2, 3, 4, 6]
                break;
            case ETextType.win:
                arr = [0, 1, 2, 3, 4, 5]
                break;
            default:
                break;
        }
        this.infoText.getComponent(lampUI)?.setHideMessage(arr);
    }

    private _type: EInfoBoardType = EInfoBoardType.normal;
    public get type(): EInfoBoardType {
        return this._type;
    }
    public set type(v: EInfoBoardType) {
        this._type = v;
        for (let i: number = 0; i < this.bgs.length; i++) {
            this.bgs[i].children[0].active = v == EInfoBoardType.free;
        }
    }

    private _winLevel: EWinLev = EWinLev.one;
    public get winLevel(): EWinLev {
        return this._winLevel;
    }
    public set winLevel(v: EWinLev) {
        if (this._winLevel == v) return;
        this._winLevel = v;
        for (let i: number = 0; i < this.bgs.length; i++) {
            let bg: Node = this.bgs[i];
            if (v == i) {
                bg.active = true;
                if (v == EWinLev.two) {
                    bg.getComponent(Animation)?.play();
                    this.playLight();
                } else if (v == EWinLev.three) {
                    bg.getComponent(Animation)?.play();
                    this.playLight();
                }
            } else {
                bg.active = false;
            }
        }
    }

    private _infoWinType: EWinType = EWinType.num;
    /** */
    public get infoWinType(): EWinType {
        return this._infoWinType;
    }
    public set infoWinType(v: EWinType) {
        this._infoWinType = v;
        (this.infoWin.getChildByName("info_win") as Node).active = v == EWinType.win;
        (this.infoWin.getChildByName("info_tw") as Node).active = v == EWinType.total;
        (this.infoWin.getChildByName("Label") as Node).active = true;
    }


    bindBgEffect() {
        for (let i: number = 0; i < this.bgEffects.length; i++) {
            this.bgEffects[i].active = i == this.winLevel;
        }
    }

    hideBgEffect() {
        for (let i: number = 0; i < this.bgEffects.length; i++) {
            this.bgEffects[i].active = false;
        }
    }

    onLoad() {

    }

    protected start(): void {
        this.type = EInfoBoardType.normal;
        this.showInfoTextBn = true;
        this.infoTextType = ETextType.normal;
        this.winLevel = EWinLev.one;
    }

    playSprays() {
        for (let i: number = 0; i < this.sprays.length; i++) {
            this.sprays[i].node.active = true;
            this.sprays[i].play();
        }
    }

    /** */
    playLight() {
        this.light.play();
    }

    /** */
    playStar() {
        this.star.play();
    }

    playInfoWin() {
        tween(this.infoWin)
            .to(0.5, { scale: new Vec3(1.4, 1.4) })
            .to(0.5, { scale: new Vec3(1, 1) })
            .start();
    }

    /**winNode label */
    private winAimCoin: number = 0;
    private winAddcoin: number = 0;
    private winAddOnce: number = 0;
    private _winPlayOverBn: boolean = false;
    public get winPlayOverBn(): boolean {
        return this._winPlayOverBn;
    }
    public set winPlayOverBn(v: boolean) {
        this._winPlayOverBn = v;
    }
    playAnimWin(coin: number, type: number = -1,) {
        if (coin > songkran.betSum * 3 && coin < songkran.betSum * 5) {
            this.winLevel = EWinLev.two;
        } else if (coin >= songkran.betSum * 5) {
            this.winLevel = EWinLev.three;
        }
        this.playInfoWin();
        let t = (type + 1) * 30;//
        this.winAimCoin = coin;
        this.winAddcoin = 0;
        this.winAddOnce = this.winAimCoin / t;
        this.winPlayOverBn = false;
        if (t == 0) {
            this.bindWinLab(coin);
            return;
        } else {
            this.bindWinLab(this.winAddcoin);
        }
        this.schedule(this.winOneChange, 0.05, macro.REPEAT_FOREVER);
    }

    bindWinLab(coin: number) {
        (this.infoWin.getChildByName("Label")?.getComponent(Label) as Label).string = Num.exchangeToThousands(songkran.exchangeRate, coin);
    }

    winOneChange() {
        this.winAddcoin += this.winAddOnce;
        if (this.winAddcoin >= this.winAimCoin) {
            this.winPlayCoinOver();
            this.bindWinLab(this.winAimCoin);
        } else {
            this.bindWinLab(this.winAddcoin);
        }
    }

    winPlayCoinOver() {
        this.unschedule(this.winOneChange);
        this.winPlayOverBn = true;
        // dragon.node.playAnimWinOver();
    }
}


export enum ETextType {
    "normal",
    "free",
    "oneMore",
    "win",
}

export enum EWinType {
    "num",
    "win",
    "total",
}

export enum EInfoBoardType {
    "normal",
    "free",
}

export enum EWinLev {
    "one" = 0,
    "two",
    "three",
}