import { _decorator, Component, AudioClip } from 'cc';
import { ModuleDef } from '../../scripts/ModuleDef';

const { ccclass, property } = _decorator;

@ccclass('songkranAudio')
export class songkranAudio extends Component {
    @property(AudioClip)
    public stopWheel = null;
    @property(AudioClip)
    public clickstart = null;

    onLoad () {
     
    }

    start () {
    }

    private urlArr:string[] = ["sound/bgNormal","sound/bgFree","sound/bigWin","sound/win"];
    playBgm (index:number) {
        tgx.AudioMgr.inst.audio.playMusicLoop(this.urlArr[index], ModuleDef.SLOTS_SONGKRAN)
    }

    playBgNormal(){
        this.playBgm(0);
    }

    playBgFree(){
        this.playBgm(1);
    }

    playBigWin(){
        this.playBgm(2);
    }

    playWin(){
        this.playBgm(3);
    }

    playclickopen () {
    }

    playclickclose () {
    }

    playStopWheel () {
        this.playEf(this.stopWheel); 
    }


    playEf (clip: any) {
        tgx.AudioMgr.inst.audio.playEffect(clip);
    }

    playclickstart () {
        this.playEf(this.clickstart); 
    }

    clearAll(){
        tgx.AudioMgr.inst.audio.clearAll();
    }
}