import { Component, _decorator } from "cc";
import { Node } from "cc";
import { SpriteFrame } from "cc";
import { Label } from "cc";
import { Sprite } from "cc";
import { Animation } from "cc";
import { tween } from "cc";
import { Tween } from "cc";
import { Button } from "cc";
import { Color } from "cc";
import { Vec3 } from "cc";
import { find } from "cc";
import { macro } from "cc";
import { songkran } from "./cSongkran";
import { EBGType } from "./songkranBg";
import { songkranFreeBegin } from "./songkranFreeBegin";
import { songkranFreeEnd } from "./songkranFreeEnd";
import { Num } from "../../core_tgx/framework/util/Num";
import { LanguageData } from 'db://assets/module_basic/scripts/language/LanguageData';


const { ccclass, property } = _decorator;
// 
@ccclass('songkranSlotCtrl')
export class songkranSlotCtrl extends Component {
    @property({ type: Node, tooltip: "" })
    public rollBtn: Node = null!;
    @property([SpriteFrame])
    public spin_Sp = [];
    @property(Node)
    public spin_AnimNode: Node = null!;
    @property(Node)
    public rollClickAni: Node = null!;
    @property(Node)
    public Btn_stopAuto: Node = null!;
    @property(Node)
    public stopRollClickAni: Node = null!;
    @property(Node)
    public Btn_free: Node = null!;
    @property(Node)
    public Btn_add: Node = null!;
    @property(Node)
    public Btn_sub: Node = null!;
    @property(Node)
    public Btn_auto: Node = null!;
    @property(Node)
    public Btn_set: Node = null!;
    @property(Node)
    public betting_panel: Node = null!;
    @property(Node)
    public Setting_panel: Node = null!;
    @property(Node)
    public coin_panel: Node = null!;
    @property(Label)
    public lblUserCoin: Label = null!;
    @property(Label)
    public lblCurBet: Label = null!;
    @property(Label)
    public winCoin_lab: Label = null!;
    @property(Node)
    public stateNode: Node = null!;
    @property(Node)
    public tipNode: Node = null!;
    @property(Node)
    public musicBtn: Node = null!;
    @property([SpriteFrame])
    public sp_settingControl = [];
    @property(Node)
    public speedBtn: Node = null!;
    @property([SpriteFrame])
    public sp_speed = [];
    @property([SpriteFrame])
    public sp2_speed = [];

    private autoNum: number = 0;
    public isPanelAnim: boolean = false;
    public isSpeed: boolean = false;

    private _bgState: EBGType = EBGType.normal;
    public get bgState(): EBGType {
        return this._bgState;
    }
    public set bgState(v: EBGType) {
        this._bgState = v;
        this.betting_panel.active = this.bgState == EBGType.normal;
        this.Setting_panel.active = this.betting_panel.active;
        this.Btn_free.active = this.bgState == EBGType.free;
        if (v == EBGType.free) {
            this.coin_panel.y = -650;
        } else {
            this.coin_panel.y = -400;
        }
    }

    private _lastNum: number = 0;
    public get lastNum(): number {
        return this._lastNum;
    }
    public set lastNum(v: number) {
        this._lastNum = v;
        this.Btn_free.children[0].active = v > 0;
        this.Btn_free.children[1].active = !this.Btn_free.children[0].active;
        if (this.Btn_free.children[0].active) {
            (this.Btn_free.children[0].getComponent(Label) as Label).string = v + "";
        }
    }

    start() {
        this.rollBtn.on(Node.EventType.MOUSE_ENTER, this.btnRollTouchStart, this);
        this.rollBtn.on(Node.EventType.MOUSE_LEAVE, this.btnRollTouchEnd, this);
        this.rollBtn.on(Node.EventType.TOUCH_START, this.btnRollClick, this);

        this.Btn_stopAuto.on(Node.EventType.MOUSE_ENTER, this.btnStopRollTouchStart, this);
        this.Btn_stopAuto.on(Node.EventType.MOUSE_LEAVE, this.btnStopRollTouchEnd, this);
        this.Btn_stopAuto.on(Node.EventType.TOUCH_START, this.btnStopRollClick, this);

        this.autoNum = 0;//
        this.isPanelAnim = false;
        this.isSpeed = false;
        // this.settingInit_Function();
        // this.initCarlender();
        this.init();
    }

    btnRollTouchStart() {
        if (!songkran.rollEnable) {
            return;
        }
        if (this.rollClickAni) {
            this.rollClickAni.active = true;
            this.rollClickAni!.getComponent(Animation)!.play();
        }
    }

    btnRollTouchEnd() {
        if (this.rollClickAni) {
            this.rollClickAni.active = false;
        }
    }

    btnRollClick(e: any) {
        if (!songkran.rollEnable) {
            return;
        }
        songkran.main.onCLick(e, 'roll');
    }

    btnStopRollTouchStart() {
        if (this.stopRollClickAni) {
            this.stopRollClickAni.active = true;
            this.stopRollClickAni!.getComponent(Animation)!.play();
        }
    }

    btnStopRollTouchEnd() {
        if (this.stopRollClickAni) {
            this.stopRollClickAni.active = false;
        }
    }

    btnStopRollClick(e: any) {
        this.onCLick_stopAuto();
    }

    //
    onCLick_startAuto() {
        if (this.autoNum > 0) {
            // this.Auto_panel.getComponent('autoPanel').hidePanel();
            // songkran.startAuto(this.autoNum);
        }
    }

    //
    onCLick_stopAuto() {
        this.Btn_stopAuto.active = false;
        this.rollBtn.active = true;
        songkran.main.stopAuto();
    }

    init() {
        (this.musicBtn.getComponent(Sprite) as Sprite).spriteFrame = this.sp_settingControl[tgx.AudioMgr.inst.switch?1:0];
        this.Setting_panel.opacity = 0;
        this.tipNode.opacity = 0;
        this.winCoin_lab.string = Num.exchangeToThousands(songkran.exchangeRate, 0);
        this.lblUserCoin.string = Num.exchangeToThousands(songkran.exchangeRate, songkran.money);
        this.setSpinAnim(0);
    }

    update() {
        this.initTip();
    }

    public sumList: number[] = [];
    //
    initBetContent(numList: number[], betList: number[], line: number) {
        //
        let list = [];
        for (let i = 0; i < numList.length; i++) {
            for (let j = 0; j < betList.length; j++) {
                list.push(Math.round(numList[i] * betList[j] * line * 100) / 100);
            }
        }
        let sumList = Array.from(new Set(list));
        sumList.sort((a, b) => {
            return a - b;
        });
        this.sumList = sumList;
    }

    settingControlButtonClick_Function() {
        if (tgx.AudioMgr.inst.switch == true) {
            tgx.AudioMgr.inst.switch = false;
            (this.musicBtn.getComponent(Sprite) as Sprite).spriteFrame = this.sp_settingControl[0];
        } else {
            tgx.AudioMgr.inst.switch = true;
            (this.musicBtn.getComponent(Sprite) as Sprite).spriteFrame = this.sp_settingControl[1];
        }
    }

    //
    onCLick(event: any, args: any) {
        songkran.main.onCLick(event, args);
    }

    //
    onCLick_openAutoPanel() {
        songkran.autoPanel.showPanel();
    }

    //
    onCLick_closeAutoPanel() {
        songkran.main.audio.playclickclose();
        // this.Auto_panel.getComponent('autoPanel').hidePanel();
    }

    private _btnContent: Node = null!;

    public get btnContent(): Node {
        if (!this._btnContent) {
            this._btnContent = find('btnContent', this.betting_panel) as Node;;
        }
        return this._btnContent;
    }


    //
    onCLickShowSettingPanel(event: any) {
        songkran.main.audio.playclickopen();
        if (this.betting_panel.opacity == 0) {
            return;
        }
        let footer = find("ui_footer/ui_footer_a", songkran.main.node);
        if (footer) {
            footer.active = false;
        }
        this.rollBtn.active = false;
        tween(this.btnContent)
            .to(0.2, { position: new Vec3(0, -180, 0), opacity: 0 })
            .start()
        tween(this.Setting_panel)
            .to(0.2, { position: new Vec3(0, -560, 0), opacity: 255 })
            .start();
    }

    //
    onCLickHideSettingPanel(event: any) {
        if (this.Setting_panel.opacity == 0) {
            return;
        }
        let footer = find("ui_footer/ui_footer_a", songkran.main.node);
        if (footer) {
            footer.active = true;
        }
        this.rollBtn.active = true;
        tween(this.btnContent)
            .to(0.2, { position: new Vec3(0, 0, 0), opacity: 255 })
            .start()
        tween(this.Setting_panel)
            .to(0.2, { position: new Vec3(0, -740, 0), opacity: 0 })
            .start();
    }

    //
    onCLick_betPanel(event: any, args: any) {
        let type = parseInt(args);
        if (this.isPanelAnim) {
            return;
        }
        this.isPanelAnim = true;
        this.scheduleOnce(() => {
            this.isPanelAnim = false;
        }, 0.5);
        if (type == 0) {
            songkran.betPanel.hidePanel();
        } else {
            songkran.betPanel.showPanel();
            songkran.betPanel.bindBet();
        }
    }

    //
    onCLick_sound(event: any) {
        this.settingControlButtonClick_Function();
    }

    //
    onCLick_chooseAutoNum(event: any, args: any) {

    }

    //
    onCLick_speed() {
        songkran.main.audio.playclickopen();
        if (this.isSpeed) {
            this.isSpeed = false;
            this.tipNode.children[2].active = true;
            this.tipNode.children[3].active = false;
            this.createTipNode(LanguageData.getLangByID("BXINGAME_33"));
            (this.speedBtn.getComponent(Sprite) as Sprite).spriteFrame = this.sp_speed[0];
            (this.speedBtn.children[0].getComponent("cc.Sprite") as Sprite).spriteFrame = this.sp2_speed[0];
            this.speedBtn.children[1].active = false;
        } else {
            this.isSpeed = true;
            this.tipNode.children[2].active = false;
            this.tipNode.children[3].active = true;
            this.createTipNode(LanguageData.getLangByID("BXINGAME_34"));
            (this.speedBtn.getComponent(Sprite) as Sprite).spriteFrame = this.sp_speed[1];
            (this.speedBtn.children[0].getComponent(Sprite) as Sprite).spriteFrame = this.sp2_speed[1];
            this.speedBtn.children[1].active = true;
        }
    }

    initTip() {
        if (this.tipNode.children[2].active || this.tipNode.children[3].active) {
            this.tipNode.children[0].w = this.tipNode.children[1].w + 70;
            this.tipNode.children[1].x = 16;
            this.tipNode.children[2].x = -this.tipNode.children[1].w / 2;
            this.tipNode.children[3].x = -this.tipNode.children[1].w / 2;
        } else {
            this.tipNode.children[1].x = 0;
            this.tipNode.children[0].w = this.tipNode.children[1].w + 30;
        }
    }

    //
    updateBet(idx: number): number {
        if (idx >= this.sumList.length || idx < 0) {
            if (idx >= this.sumList.length) {
                this.tipNode.children[2].active = false;
                this.tipNode.children[3].active = false;
                this.createTipNode(LanguageData.getLangByID("BXINGAME_35"));
            } else {
                this.tipNode.children[2].active = false;
                this.tipNode.children[3].active = false;
                this.createTipNode(LanguageData.getLangByID("BXINGAME_36"));
            }
            return 0;
        }

        if (songkran.sumIdx > 0) {
            this.Btn_sub.opacity = 255;
        }
        if (songkran.sumIdx < this.sumList.length - 1) {
            this.Btn_add.opacity = 255;
        }
        if (songkran.sumIdx == 0) {
            this.Btn_sub.opacity = 100;
        }
        if (songkran.sumIdx == this.sumList.length - 1) {
            this.Btn_add.opacity = 100;
        }
        songkran.betSum = this.sumList[idx];
        this.lblCurBet.string = Num.exchangeToThousands(songkran.exchangeRate, songkran.betSum);
        this.curBetAni();
        return 1;
    }

    private curBetTween: Tween<Node> | null = null;
    private curBetAni() {
        this.curBetTween?.stop();
        this.curBetTween?.destroySelf();
        this.curBetTween = tween(this.lblCurBet.node)
            .to(0.1, { scale: new Vec3(1.6, 1.6, 1.6) })
            .to(0.1, { scale: new Vec3(1, 1, 1) })
            .to(0.1, { scale: new Vec3(1.2, 1.2, 1.2) })
            .to(0.1, { scale: new Vec3(1, 1, 1) })
            .start()
    }

   

    //
    setSpinAnim(type: number) {//0-  1-   2-   3-
        switch (type) {
            case 0:
                // this.Btn_start.getComponent(cc.Button).interactable = true;
                songkran.rollEnable = true;
                (this.spin_AnimNode.getComponent(Sprite) as Sprite).spriteFrame = this.spin_Sp[0];
                (this.spin_AnimNode.getComponent(Animation) as Animation).getState("xuanzhuan").speed = 1;
                !songkran.auto && this.setButtonState(true);
                // if (songkran.lotteryRes && songkran.lotteryRes.winscore == 0) {
                this.changeLabColor(1);
                // }
                break;
            case 1:
                // this.Btn_start.getComponent(cc.Button).interactable = false;
                songkran.rollEnable = false;
                ((this.rollBtn.getChildByName("VFX") as Node).getComponent(Animation) as Animation).play();
                let VFX2Ani: Animation = this.rollBtn.getChildByName("VFX")?.getComponent(Animation) as Animation;
                VFX2Ani.play();
                (this.spin_AnimNode.getComponent(Sprite) as Sprite).spriteFrame = this.spin_Sp[2];
                (this.spin_AnimNode.getComponent(Animation) as Animation).getState("xuanzhuan").speed = 10;
                this.winCoin_lab.string = Num.exchangeToThousands(songkran.exchangeRate, 0);
                this.setButtonState(false);
                this.changeLabColor(0);
                break;
            case 2:
                // this.Btn_start.getComponent(cc.Button).interactable = false;
                songkran.rollEnable = false;
                (this.spin_AnimNode.getComponent(Sprite) as Sprite).spriteFrame = this.spin_Sp[0];
                (this.spin_AnimNode.getComponent(Animation) as Animation).getState("xuanzhuan").speed = 1;
                break;
            case 3:
                // this.Btn_start.getComponent(cc.Button).interactable = false;
                songkran.rollEnable = false;
                (this.spin_AnimNode.getComponent(Sprite) as Sprite).spriteFrame = this.spin_Sp[1];
                (this.spin_AnimNode.getComponent(Animation) as Animation).getState("xuanzhuan").speed = 0;
                break;
        }
    }

    /** */
    setButtonState(state: boolean) {
        this.Btn_add.opacity = state ? 255 : 100;
        this.Btn_sub.opacity = state ? 255 : 100;
        this.Btn_auto.opacity = state ? 255 : 100;
        this.Btn_set.opacity = state ? 255 : 100;
        ((this.Btn_add as Node).getComponent(Button) as Button).interactable = state;
        ((this.Btn_sub as Node).getComponent(Button) as Button).interactable = state;
        ((this.Btn_auto as Node).getComponent(Button) as Button).interactable = state;
        ((this.Btn_set as Node).getComponent(Button) as Button).interactable = state;
        ((this.lblCurBet.node.parent.parent as Node).getComponent(Button) as Button).interactable = state;
    }

    /** 0: other*/
    changeLabColor(type: number) {
        if (type == 0) {
            this.lblUserCoin.node.color = new Color(255, 255, 255);
            this.lblCurBet.node.color = new Color(255, 255, 255);
            this.winCoin_lab.node.color = new Color(255, 255, 255);
        } else {
            this.lblUserCoin.node.color = new Color(123, 233, 246);
            this.lblCurBet.node.color = new Color(123, 233, 246);
            this.winCoin_lab.node.color = new Color(123, 233, 246);
        }
    }


    //
    private tipTween: Tween<Node> | null = null;
    createTipNode(word: string) {
        this.tipTween?.stop();
        this.tipTween?.destroySelf();
        this.tipNode.opacity = 0;
        this.tipNode.scale = new Vec3(1, 1, 1);
        (((this.tipNode as Node).getChildByName("lab") as Node).getComponent(Label) as Label).string = word;
        this.tipTween = tween(this.tipNode)
            .call(() => { this.tipNode.opacity = 255 })
            .to(0.1, { scale: new Vec3(1.2, 1.2, 1.2) })
            .to(0.1, { scale: new Vec3(1, 1, 1) })
            .delay(2)
            .to(0.2, { opacity: 0 })
            .start()
    }

    /**winNode label */
    private winAimCoin: number = 0;
    private winAddcoin: number = 0;
    private winAddOnce: number = 0;
    private _winPlayOverBn: boolean = false;
    public get winPlayOverBn(): boolean {
        return this._winPlayOverBn;
    }
    public set winPlayOverBn(v: boolean) {
        this._winPlayOverBn = v;
    }
    playAnimWin(sCoin: number, eCoin: number, type: number = -1) {
        if (eCoin == 0) {
            this.winCoin_lab.string = "0";
            return;
        }
        let t = (type + 1) * 30;//
        this.winAimCoin = eCoin;
        this.winAddcoin = sCoin;
        this.winAddOnce = (eCoin - sCoin) / t;
        this.winPlayOverBn = false;
        if (t == 0) {
            this.bindWinLab(eCoin);
            return;
        } else {
            this.bindWinLab(this.winAddcoin);
        }
        this.schedule(this.winOneChange, 0.05, macro.REPEAT_FOREVER);
    }

    bindWinLab(coin: number) {
        this.winCoin_lab.string = Num.exchangeToThousands(songkran.exchangeRate, coin);
        this.lblUserCoin.string = Num.exchangeToThousands(songkran.exchangeRate, songkran.lotteryRes.userscore - songkran.lotteryRes.winscore + coin);
    }

    winOneChange() {
        this.winAddcoin += this.winAddOnce;
        if (this.winAddcoin >= this.winAimCoin) {
            this.winPlayCoinOver();
            this.bindWinLab(this.winAimCoin);
        } else {
            this.bindWinLab(this.winAddcoin);
        }
    }

    winPlayCoinOver() {
        this.unschedule(this.winOneChange);
        this.winPlayOverBn = true;
    }
}
