import { Node } from "cc";
import { Vec3 } from "cc";
import { Label } from "cc";
import { tween } from "cc";
import { Component } from "cc";
import { _decorator } from "cc";
import { songkran } from "./cSongkran";
import { macro } from "cc";
import { Num } from "../../core_tgx/framework/util/Num";


const { ccclass, property } = _decorator;

@ccclass('songkranFreeEnd')
export class songkranFreeEnd extends Component {
    @property({ type: Label, tooltip: "" })
    public freeTimes: Label = null!;
    @property({ type: Node, tooltip: "" })
    public btn: Node = null!;
    @property({ type: Label, tooltip: "" })
    public num: Label = null!;

    onLoad() {

    }

    protected start(): void {
        this.freeShow(songkran.lotteryRes.rsts.length - 1, songkran.lotteryRes.winscore);
    }

    freeShow(freeTimes: number, coin: number) {
        this.node.active = true;
        this.freeTimes.string = freeTimes + "";
        this.btn.opacity = 0;
        this.btn.scale = new Vec3(0, 0);
        this.playCoin(coin);
    }

    //
    private aimCoin: number = 0;
    private addcoin: number = 0;
    private addOnce: number = 0;
    private _playOverNumBn: boolean = true;
    public get playOverNumBn(): boolean {
        return this._playOverNumBn;
    }
    public set playOverNumBn(v: boolean) {
        this._playOverNumBn = v;
    }

    playCoin(coin: number) {
        this.aimCoin = coin;
        this.addcoin = 0;
        this.addOnce = this.aimCoin / 100;
        this.playOverNumBn = false;
        this.num.string = Num.exchangeToThousands(songkran.exchangeRate, this.addcoin);
        this.schedule(this.coinOneChange, 0.05, macro.REPEAT_FOREVER);
        this.scheduleOnce(this.showBtn, 8);
    }

    coinOneChange() {
        this.addcoin += this.addOnce;
        if (this.addcoin >= this.aimCoin) {
            this.playCoinOver();
        } else {
            this.num.string = Num.exchangeToThousands(songkran.exchangeRate, this.addcoin);
        }
    }

    playCoinOver() {
        this.unschedule(this.coinOneChange);
        this.playOverNumBn = true;
        this.num.string = Num.exchangeToThousands(songkran.exchangeRate, this.aimCoin);
        this.unschedule(this.showBtn);
        this.showBtn();
    }

    private showBtn() {
        this.btn.opacity = 255;
        tween(this.btn)
            .to(0.5, { scale: new Vec3(1.2, 1.2) })
            .to(0.1, { scale: new Vec3(1, 1) })
            .call(() => {
                this.playOverBn = true;
                this.scheduleOnce(this.freeHide, 5)
            })
            .start();
    }

    private playOverBn: boolean = false;
    freeHide() {
        if (this.playOverBn) {
            this.node.destroy();
            songkran.freeEnd = undefined;
            songkran.freeEndPB.decRef();
            songkran.main.freeEndHide();
        }

    }
}
