import { tween } from "cc";
import { Component, _decorator } from "cc";
import { Node } from "cc";
import { Label } from "cc";
import { Color } from "cc";
import { EventTouch } from "cc";
import { Num } from "../../core_tgx/framework/util/Num";
import { songkran } from "./cSongkran";
const { ccclass, property } = _decorator;

@ccclass('autoPanel')
export class autoPanel extends Component {
    @property({ type: Node, tooltip: "" })
    public panel: Node = null!;
    @property({ type: Node, tooltip: "" })
    public roundedButton: Node = null!;
    @property({ type: Label, tooltip: "" })
    public lblUserCoin: Label = null!;
    @property({ type: Label, tooltip: "" })
    public lblCurBet: Label = null!;
    @property({ type: Label, tooltip: "" })
    public winCoin_lab: Label = null!;

    autoNum: number = 0;

    init() {
        songkran.autoPanel = this;
        this.node.parent.children[0].active = false;
        this.node.active = false;

    }

    start() {
        this.init();
    }

    protected update(dt: number): void {
        this.lblUserCoin.string = Num.exchangeToThousands(songkran.exchangeRate, songkran.money);
        this.lblCurBet.string = Num.exchangeToThousands(songkran.exchangeRate,songkran.betSum);
        this.winCoin_lab.string = songkran.slotCtrl.winCoin_lab.string;
    }

    private autoChooseColor: number[] = [243, 168, 85];
    //
    onCLick_chooseAutoNum(event: EventTouch, args: any) {
        this.roundedButton.color = new Color(255, 200, 36);
        this.roundedButton.opacity = 255
        this.autoNum = parseInt(args);
        let chooseNode = event.target.parent;
        for (let i in chooseNode.children) {
            chooseNode.children[i].children[0].children[0].color = new Color(100, 100, 110);
        }
        event.target.children[0].children[0].color = new Color(this.autoChooseColor[0], this.autoChooseColor[1], this.autoChooseColor[2]);
    }

    //
    onCLick_startAuto() {
        if (this.autoNum > 0) {
            this.onClose();
            songkran.main.startAuto(this.autoNum);
        }
    }

    private showPos: number;
    private hidePos: number;
    showPanel() {
        songkran.main.audio.playclickopen();
        this.node.parent.children[0].active = true;
        this.node.active = true;
        this.showPos = this.panel.h / 2;
        this.hidePos = -this.panel.h / 2;
        this.panel.y = this.hidePos;
        tween(this.panel)
            .to(0.2, { y: this.showPos })
            .start()
        tween(this.node)
            .delay(0.2)
            .call(() => {
                tween(this.panel.children[0])
                    .to(0.2, { opacity: 255 })
                    .start()
            })
            .start()
    }

    hidePanel() {
        tween(this.panel)
            .to(0.2, { y: this.hidePos })
            .call(() => {
                this.node.parent.children[0].active = false;
                this.node.active = false;
            })
            .start()
    }

    onClose() {
        songkran.main.audio.playclickclose();
        this.hidePanel();
    }
}
