    /** */
    drop() {
        let removes: number[] = songkran.waveData.winCards;
        let symbols: baseSymbol[] = this.getSymbols;
        if (removes.length > 0) {
            songkran.removeCount = 0;
            for (let i = 0; i < removes.length; i++) {
                let remove: number = removes[i];
                let symbol: songkranSymbol = symbols[remove] as songkranSymbol;
                if (symbol) {
                    symbol.playClear();
                } else {
                    Logger.error("roundIndex" + songkran.waveIndex + "index:" + remove);
                }
            }
        }
    }

    private addBn: boolean = false;
    public doAdd() {
        if (this.addBn) return;
        this.addBn = true;
        this.scheduleOnce(() => {
            this.addBn = false;
        }, 2);
        songkran.removeCount = undefined;
        this.scheduleOnce(() => {
            this.addItems()
        }, 0.5);
    }

    private addItems() {
        this.removeUnuseAll();
        let arr: number[] = [5, 5, 5, 5, 5];
        let addItems: addInfo[] = songkran.waveData.addCards;
        for (let i = 0; i < addItems.length; i++) {
            let addItem: addInfo = addItems[i];
            let r: reelDate = this.reelDates[addItem.rindex]
            let symbol: songkranSymbol = this.pushSymbol(r, addItem.id) as songkranSymbol;
            symbol.aimBn = true;
            symbol.bindAimPos(arr[addItem.rindex])
            arr[addItem.rindex]++;
        }
        this.scheduleOnce(this.playDrop, 1);

    }

    private playDrop() {
        for (let i = 0; i < this.reelDates.length; i++) {
            let r: reelDate = this.reelDates[i];
            for (let j = 0; j < r.symbols.length; j++) {
                let symbol: baseSymbol = r.symbols[j];
                this.dropAim(symbol);
            }
        }
        this.scheduleOnce(() => { songkran.main.dropBack(); }, 1);
    }

    private dropAim(symbol: baseSymbol) {
        if (Math.abs(symbol.aimPos - symbol.node.y) > 1) {
            tween(symbol.node)
                .to(0.2, { y: symbol.aimPos })
                .by(0.1, { y: -this.reboundDis }, { easing: 'quartOut' })
                .by(0.1, { y: this.reboundDis }, { easing: 'quartIn' })
                .start();
        }
    }

    spawn() {
        let symbols: songkranSymbol[] = this.getSymbols as songkranSymbol[];
        for (let i = 0; i < symbols.length; i++) {
            let symbol: songkranSymbol = symbols[i];
            symbol.spawn();
        }
    }
