import { _decorator, Component, Enum, Node, sp, Sprite, SpriteFrame } from 'cc';
import { instantiate } from 'cc';
import { Prefab } from 'cc';
import { tween } from 'cc';
import { Vec3 } from 'cc';
import { Animation } from 'cc';
import { baseSymbol, ESHOWTYPE } from '../../module_basic/ui_slot/match3/baseSymbol';
import { songkran } from './cSongkran';

const { ccclass, property } = _decorator;

@ccclass('songkranSymbol')
export class songkranSymbol extends baseSymbol {
    @property({ type: Prefab, tooltip: "" })
    public boom: Prefab = null!;
    @property({ type: Prefab, tooltip: "" })
    public select: Prefab = null!;
    @property({ type: Prefab, tooltip: "" })
    public clear: Prefab = null!;

    onLoad() {

    }
    unuse(): void {
        this.selectBn = false;
        super.unuse();
    }

    private _selectBn: boolean = false;
    public get selectBn(): boolean {
        return this._selectBn;
    }
    public set selectBn(v: boolean) {
        this._selectBn = v;
        let name: string = "select";
        let node: Node = this.node.getChildByName(name);
        if (v) {
            if (!node) {
                node = instantiate(this.select);
                node.name = name;
                node.parent = this.node;
                node.setSiblingIndex(0);
            }
        } else {
            if (node) {
                node.destroy();
            }
        }
    }

    /** */
    playBoom() {
        if (this.selectBn == false) return;
        let ani: Animation;
        ani = instantiate(this.boom).getComponent(Animation);
        ani.node.parent = this.node;
        ani.on("finished" as any, () => {
            ani.node.destroy();
        }, this);
        ani.play();
    }

    playClear() {
        let ani: Animation;
        ani = instantiate(this.clear).getComponent(Animation);
        ani.node.parent = this.node;
        ani.on("finished" as any, () => {
            ani.node.destroy();
            this.recover();
            songkran.removeCount++;
            if (songkran.removeCount >= songkran.waveData.addCards.length)
                this.scheduleOnce(() => {
                    songkran.reel.doAdd();
                }, 0.1);
        }, this);
        ani.play();
    }

    /** wild*/
    spawn() {
        // if (this.symbolId != ESYMBOLID.wild && this.symbolId != ESYMBOLID.scatter) return;
        this.showType = ESHOWTYPE.ske;
        this.playAni(ESYMBOLANI.spawn, false);
        this.addAni(ESYMBOLANI.idle, true);
        if (this.symbolId == ESYMBOLID.wild) {
            // songkran.main.audio.playSpawnWild();
        }
        if (this.symbolId == ESYMBOLID.scatter) {
            // songkran.main.audio.playSpawnScatter();
        }
    }
    /** */
    playWin() {
        this.showType = ESHOWTYPE.ske;
        this.playAni(ESYMBOLANI.win, true);
        this.selectBn = true;
    }
    onClick(e: TouchEvent) {
        songkran.main.addDetail(this);
    }
}

export enum ESYMBOLID {
    null,
    nine,
    ten,
    J,
    Q,
    K,
    A,
    ball,
    eyewear,
    ladle,
    horse,
    girl,
    man,
    wild,
    scatter
}

export enum ESYMBOLANI {
    win = "win",                  //
    idle = "idle",                //
    spawn = "spawn",              //wild 
}


