import { Vec3 } from 'cc';
import { Animation } from 'cc';
import { ccenum } from 'cc';
import { tween } from 'cc';
import { _decorator, Component } from 'cc';

ccenum(Number);
const { ccclass, property } = _decorator;
@ccclass('clearRow')
export class clearRow extends Component {
    @property({ type: Animation })
    public ani: Animation = null!;

    onLoad() {

    }

    start() {
    }

    private startTime: number = 0.16;
    private moveTime: number = 0.32;
    play(aimY: number, callBack: Function) {
        this.ani.play();
        tween(this.node)
            .delay(this.startTime)
            .to(this.moveTime, { y: aimY })
            .call(() => {
                callBack();
                this.node.parent = null;
            })
            .start();
    }
}