import { _decorator, Component, macro, sp } from 'cc';
import { Node } from 'cc';
import { Num } from '../../core_tgx/framework/util/Num';
import { LanguageSprite } from 'db://assets/module_basic/scripts/language/LanguageSprite';
import { Vec3 } from 'cc';
import { Label } from 'cc';
import { Animation } from 'cc';
import { SpriteFrame } from 'cc';
import { Sprite } from 'cc';

import { tween } from 'cc';
import { songkran } from './cSongkran';
const { ccclass, property } = _decorator;

@ccclass('songkranBigwin')
export class songkranBigwin extends Component {
 
    @property({ type: Label, tooltip: "" })
    public bigWinCoin_lab: Label = null!;
    onLoad() {
    }
    protected start(): void {
        this.node.children[0].active = false;
        this.node.children[1].active = false;
        this.node.children[2].active = false;
        this.node.children[3].active = false;
        this.playBigWinCoin(songkran.rollData.winscore);
    }
   

    //
    private aimCoin: number = 0;
    private addcoin: number = 0;
    private addOnce: number = 0;
    private _playOverBn: boolean = true;
    public get playOverBn(): boolean {
        return this._playOverBn;
    }
    public set playOverBn(v: boolean) {
        this._playOverBn = v;
    }

    playBigWinCoin(coin: number) {
        this.fastBn = true;
        songkran.main.audio.playBigWin();
        songkran.main.unschedule(songkran.main.sendRoll);
        this.bigBn = false;
        this.superBn = false;
        this.megaBn = false;
        this.aimCoin = coin;
        this.addcoin = 0;
        this.addOnce = this.aimCoin / this.changeNum(coin);
        this.playOverBn = false;
        this.bigWinCoin_lab.string = Num.exchangeToThousands(songkran.exchangeRate, this.addcoin);
        this.schedule(this.coinOneChange, 0.05, macro.REPEAT_FOREVER);
        this.scheduleOnce(this.closeBigWin, 10);
    }

    changeNum(coin: number): number {
        if (coin >= songkran.betSum * 35) {
            return 160;
        } else if (coin >= songkran.betSum * 15 && coin < songkran.betSum * 35) {
            return 130;
        } else {
            return 100;
        }
    }

    private bigBn: boolean = false;
    private superBn: boolean = false;
    private megaBn: boolean = false;
    coinOneChange() {
        this.addcoin += this.addOnce;
        if (this.addcoin >= this.aimCoin) {
            this.playBigWin(this.aimCoin);
            this.playCoinOver();
        } else {
            this.playBigWin(this.addcoin);
            this.bigWinCoin_lab.string = Num.exchangeToThousands(songkran.exchangeRate, this.addcoin);
        }
    }

    playCoinOver() {
        this.unschedule(this.coinOneChange);
        this.playOverBn = true;
        this.playBigWin(this.aimCoin);
        this.bigWinCoin_lab.string = Num.exchangeToThousands(songkran.exchangeRate, this.aimCoin);
        this.unschedule(this.closeBigWin);
        this.scheduleOnce(this.closeBigWin, 2);
    }

    playBigWin(coin: number) {
        if (coin >= songkran.betSum * 35 && !this.node.children[3].active && this.megaBn == false) {
            this.playAnim_MegaWin();
            this.megaBn = true;
        } else if (coin >= songkran.betSum * 15 && coin < songkran.betSum * 35 && !this.node.children[2].active && this.superBn == false) {
            this.playAnim_SuperWin();
            this.superBn = true;
        } else if (this.bigBn == false) {
            this.playAnim_BigWin(-1);
            this.bigBn = true;
        }
    }

    private fastBn: boolean = false;
    onFastBigWin(e: TouchEvent) {
        if (this.fastBn == false) return;
        if (this.playOverBn) {
            this.unschedule(this.closeBigWin);
            this.doCloseBigWin();
            this.fastBn = false;
        } else {
            this.playCoinOver();
        }
    }

    //BigWin
    playAnim_BigWin(coin: number = 0) {
        this.node.active = true;
        this.node.children[0].active = true;
        (this.node.children[0].getComponent(Animation) as Animation).play();
        this.node.children[1].active = true;
        this.node.children[2].active = false;
        this.node.children[3].active = false;
        (this.node.children[1].getComponent(Animation) as Animation).play();
    }

    //SuperWin
    playAnim_SuperWin() {
        this.node.active = true;
        this.node.children[0].active = true;
        (this.node.children[0].getComponent(Animation) as Animation).play();
        this.node.children[1].active = false;
        this.node.children[2].active = true;
        this.node.children[3].active = false;
        try {
            (this.node.children[2].getComponent(Animation) as Animation).play();
        } catch (e) {
            console.log(e);
        }
    }

    //MegaWin
    playAnim_MegaWin() {
        this.node.active = true;
        this.node.children[0].active = true;
        (this.node.children[0].getComponent(Animation) as Animation).play();
        this.node.children[1].active = false;
        this.node.children[2].active = false;
        this.node.children[3].active = true;
        try {
            (this.node.children[3].getComponent(Animation) as Animation).play();
        } catch (e) {
            console.log(e);
        }
    }

    closeBigWin() {
        this.playCoinOver();
        this.unschedule(this.closeBigWin);
        this.unschedule(this.coinOneChange);
        this.scheduleOnce(this.doCloseBigWin, 5);
    }

    doCloseBigWin() {
        this.unschedule(this.doCloseBigWin);
        this.node.children[0].active = false;
        this.node.children[1].active = false;
        this.node.children[2].active = false;
        this.node.children[3].active = false;
        tween(this.node)
            .to(1, { opacity: 0 })
            .call(() => {
                songkran.bigwin.node.destroy();
                songkran.bigwin = undefined;
                songkran.bigwinPB.decRef();
                songkran.main.closeBigWin();
            })
            .start();
    }
}