
import { instantiate } from "cc";
import { autoPanel } from "./autoPanel";
import { betPanel } from "./betPanel";
import { dataChange, oldRes, rst, wave } from "./dataChange";
import { lastmoneyPanel } from "./lastmoneyPanel";
import { songkranDetail } from "./songkranDetail";
import { songkranFreeBegin } from "./songkranFreeBegin";
import { songkranFreeEnd } from "./songkranFreeEnd";
import { songkranMain } from "./songkranMain";
import { songkranReel } from "./songkranReel";
import { songkranSlotCtrl } from "./songkranSlotCtrl";
import { songkranBigwin } from "./songkranBigwin";
import { resources } from "cc";
import { Prefab } from "cc";
import { ResourceMgr } from "../../core_tgx/base/ResourceMgr";
import { ModuleDef } from "../../scripts/ModuleDef";
import { Logger } from "../../core_tgx/base/log/Logger";
class cSongkran {
    public static _instance: cSongkran | null = null;

    public static get instance(): cSongkran {
        if (!this._instance) {
            this._instance = new cSongkran();
        }
        return this._instance;
    }

    public BETNUM = dataChange.BETNUM;
    public LINES = dataChange.LINES;
    public BET = dataChange.BET;

    private _isNewUser: boolean = false;
    public get isNewUser(): boolean {
        this._isNewUser = false;
        return this._isNewUser;
    }

    private _money: number = 0;
    public get money(): number {
        return this._money;
    }
    public set money(m: number) {
        this._money = m;
    }

    private _exchangeRate: number = 0;
    public get exchangeRate(): number {
        this._exchangeRate = 1;
        return this._exchangeRate;
    }

    public detail: songkranDetail | null = null;
    public reel: songkranReel;
    public main: songkranMain = null!;
    public get slotCtrl(): songkranSlotCtrl {
        return this.main.slotCtrl;
    }
    /** */
    public betPanel: betPanel = null!;
    /** */
    public autoPanel: autoPanel = null!;
    /** */
    public lastmoneyPanel: lastmoneyPanel = null!;
    public bet: number = 0;
    public betNum: number = 0;
    public sumIdx: number = 0;

    /** */
    public betSum: number = 0;

    /** */
    public lotteryRes: oldRes = null!;

    /** */
    public auto: boolean = false;
    /** */
    public autoTimes: number = 0;
    /**012 */
    public status: number = 0;
    /** */
    public winLinePos: number[] = [];
    /** */
    public bigWinNum: number = 0;
    /** */
    public rollIndex: number = 0;
    public get rollData(): rst {
        return songkran.lotteryRes.rsts[songkran.rollIndex];
    }
    public waveIndex: number = 0;
    public get waveData(): wave {
        return this.rollData.waves[this.waveIndex];
    }

    public rollEnable: boolean = true;

    public get bIsFreeGame(): boolean {
        if (this.lotteryRes) {
            return this.lotteryRes.rsts.length > 1 && this.rollIndex < this.lotteryRes.rsts.length - 1;
        } else {
            return false;
        }
    }

    LINENUM: number[] = [3, 4, 5, 4, 3];

    public _accumulative: number = 0;
    /** */
    public get accumulative(): number {
        return this._accumulative;
    }
    public set accumulative(v: number) {
        this.slotCtrl.playAnimWin(this._accumulative, v, 0);
        this._accumulative = v;
    }
    public removeCount: number;

    freeBeginPB: Prefab;
    private _freeBegin: songkranFreeBegin;
    public get freeBegin(): songkranFreeBegin {
        if (!this._freeBegin) {
            let url:string = "prefab/freeBegin";
            ResourceMgr.inst.loadResByModuleBundle(ModuleDef.SLOTS_SONGKRAN, url, Prefab, (error: Error, loadedResource) => {
                if (error) {
                    Logger.error('Prefab, :' + url);
                    return;
                }
                if (!(loadedResource instanceof Prefab)) {
                    Logger.error(this, 'Prefab, ?');
                    return;
                }
                this.freeBeginPB = loadedResource;
                this._freeBegin = instantiate(loadedResource).getComponent(songkranFreeBegin);
                this._freeBegin.node.parent = this.slotCtrl.node;
                this._freeBegin.node.setSiblingIndex(this.slotCtrl.coin_panel.getSiblingIndex())
                loadedResource.addRef();
            });
        }
        return this._freeBegin;
    }

    public set freeBegin(v: songkranFreeBegin) {
        this._freeBegin = v;
    }

    freeEndPB: Prefab;
    private _freeEnd: songkranFreeEnd;
    public get freeEnd(): songkranFreeEnd {
        if (!this._freeEnd) {
            let url:string = "prefab/freeEnd";
            ResourceMgr.inst.loadResByModuleBundle(ModuleDef.SLOTS_SONGKRAN, url, Prefab, (error: Error, loadedResource) => {
                if (error) {
                    Logger.error('Prefab, :' + url);
                    return;
                }
                if (!(loadedResource instanceof Prefab)) {
                    Logger.error(this, 'Prefab, ?');
                    return;
                }
                this.freeEndPB = loadedResource;
                this._freeEnd = instantiate(loadedResource).getComponent(songkranFreeEnd);
                this._freeEnd.node.parent = this.slotCtrl.node;
                loadedResource.addRef();
            })
        }
        return this._freeEnd;
    }

    public set freeEnd(v: songkranFreeEnd) {
        this._freeEnd = v;
    }

    bigwinPB:Prefab;
    private _bigwin: songkranBigwin;
    public get bigwin(): songkranBigwin {
        if (!this._bigwin) {
            let url:string = "prefab/bigWin";
            ResourceMgr.inst.loadResByModuleBundle(ModuleDef.SLOTS_SONGKRAN, url, Prefab, (error: Error, loadedResource) => {
                if (error) {
                    Logger.error('Prefab, :' + url);
                    return;
                }
                if (!(loadedResource instanceof Prefab)) {
                    Logger.error(this, 'Prefab, ?');
                    return;
                }
                this.bigwinPB = loadedResource;
                this._bigwin = instantiate(loadedResource).getComponent(songkranBigwin);
                this._bigwin.node.parent = this.slotCtrl.node;
                loadedResource.addRef();
            })
        }
        return this._bigwin;
    }

    public set bigwin(v: songkranBigwin) {
        this._bigwin = v;
    }

    constructor() {

    }
}

export let songkran = cSongkran.instance;