import { Node } from "cc";
import { math } from "cc";
import { Animation } from "cc";
import { instantiate } from "cc";
import { UITransform } from "cc";
import { Vec3 } from "cc";
import { Label } from "cc";
import { Tween } from "cc";
import { tween } from "cc";
import { Component } from "cc";
import { _decorator } from "cc";
import { songkran } from "./cSongkran";
import { EInfoBoardType, ETextType } from "./infoBoard";


const { ccclass, property } = _decorator;

@ccclass('songkranBg')
export class songkranBg extends Component {
    @property({ type: Node, tooltip: "normalBg" })
    public normalBg: Node = null!;
    @property({ type: Node, tooltip: "freeBg" })
    public freeBg: Node = null!;
    @property({ type: [Node], tooltip: "flags" })
    public flags: Node[] = [];
    @property({ type: [Animation], tooltip: "" })
    public faucetWinAni: Animation[] = [];
    @property({ type: [Node], tooltip: "" })
    public mulNums: Node[] = [];
    @property({ type: Animation, tooltip: "" })
    public flyNumBoomAni: Animation = null!;

    private _bgState: EBGType = EBGType.normal;
    public get bgState(): EBGType {
        return this._bgState;
    }
    public set bgState(v: EBGType) {
        this._bgState = v;
        this.normalBg.active = this.bgState == EBGType.normal;
        this.freeBg.active = this.bgState == EBGType.free;
        this.normalBg.active && this.playFloating();
        if (v == EBGType.free) {
            songkran.main.infoBoard.type = EInfoBoardType.free;
            songkran.main.infoBoard.infoTextType = ETextType.free;
        } else {
            songkran.main.infoBoard.type = EInfoBoardType.normal;
            songkran.main.infoBoard.infoTextType = ETextType.normal;

        }
        songkran.slotCtrl.bgState = v;
    }

    onLoad() {

    }

    protected start(): void {
        this.bgState = EBGType.normal;
        this.initMulNums();
        this.flyNumBoomAni.node.active = false;
    }

    private fTime: number = 3;
    private rTime: number = 2;
    private rRotation: number = 10;
    private floating(node: Node, iRotation: number = 0) {
        Tween.stopAllByTarget(node);
        let aTime: number = this.fTime + this.rTime * math.random();
        let aRotation: number = this.rRotation * math.random() + iRotation - this.rRotation / 2;
        tween(node)
            .to(aTime, { angle: aRotation })
            .call(() => {
                this.floating(node, iRotation);
            })
            .start();
    }

    private playFloating() {
        for (let i: number = 0; i < this.flags.length; i++) {
            let flag: Node = this.flags[i];
            if (i == 0) {
                this.floating(flag, -10);
            } else {
                this.floating(flag);
            }
        }
    }

    /** */
    playFaucetWin(i: number) {
        this.faucetWinAni[i].play();
    }

    initMulNums() {
        for (let i = 0; i < this.mulNums.length; i++) {
            this.mulNums[i].opacity = 0;
            (this.mulNums[i].getComponent(Label) as Label).string = "x" + "0";
        }
    }

    /** */
    fly(multiplesL: number[]) {
        for (let i = 0; i < multiplesL.length; i++) {
            let aim: number = multiplesL[i];
            if (aim > 0) {
                let numNode = this.mulNums[i];
                (numNode.getComponent(Label) as Label).string = "x" + aim;
                this.oneFly(numNode, i);
                numNode.opacity = 150;
            }
        }
        this.scheduleOnce(() => { this.doFly(multiplesL) }, 0.5)
    }

    private oneFlyAim: Vec3 = new Vec3(0, -20);
    private oneFly(numNode: Node, index: number) {
        let item: Node = instantiate(numNode);
        item.parent = numNode.parent;
        let pos: Vec3 = new Vec3(0, 0, 0);
        switch (index) {
            case 0:
                pos.x = item.x - 20;
                pos.y = item.y;
                break;
            case 1:
                pos.x = item.x;
                pos.y = item.y + 20;
                break;
            case 2:
                pos.x = item.x + 20;
                pos.y = item.y;
                break;
            default:
                break;
        }
        tween(item)
            .to(0.2, { position: pos })
            .to(0.4, { position: this.oneFlyAim })
            .call(() => {
                item.parent = null;
            })
            .start();
    }

    private doFlyAim: Vec3 = new Vec3(0, -328);
    private doFly(multiplesL: number[]) {
        this.flyNumBoomAni.node.active = true;
        this.flyNumBoomAni.play();
        let item: Node = instantiate(this.mulNums[1]);
        item.parent = this.mulNums[1].parent;
        (item.getComponent(Label) as Label).string = "x" + (multiplesL[0] + multiplesL[1] + multiplesL[2]);
        item.scale = new Vec3(0, 0);
        item.position = this.oneFlyAim;
        item.opacity = 255;
        let transForm: UITransform = item.getComponent(UITransform) as UITransform;
        var pos = transForm.convertToWorldSpaceAR(new Vec3(0, 0));
        transForm = (this.node.parent as Node).getComponent(UITransform) as UITransform;
        let p = transForm.convertToNodeSpaceAR(pos);
        item.position = p;
        item.parent = this.node.parent as Node;
        tween(item)
            .to(0.2, { scale: new Vec3(4, 4) })
            .delay(0.4)
            .to(0.4, { scale: new Vec3(1, 1), position: this.doFlyAim })
            .call(() => {
                item.parent = null;
                songkran.main.flyNumBack();
            })
            .start();
        this.scheduleOnce(() => {
            songkran.main.dropBlue()
        }, 1);
    }

    private jumpTime: number = 0.2;
    /** */
    jump(multiples: number[], i: number) {
        i -= 1;
        let aim: number = multiples[i];
        let numNode = this.mulNums[i];
        let count: number = aim - this.getNum(numNode);
        if (count > 0) {
            numNode.active = true;
            numNode.opacity = 255;
            this.playFaucetWin(i);
            let t = this.jumpTime / count;
            this.dojump(numNode, t, count);
        } else{
            if(songkran.rollIndex==0){
                numNode.active = false;
                numNode.opacity = 255;
                (numNode.getComponent(Label) as Label).string = "x" +0;
            }else{
                numNode.opacity = 255;
            }
        }
    }

    private dojump(node: Node, t: number, count: number) {
        let aimY: number = node.y;
        if (count == 0) {
            return;
        }
        (node.getComponent(Label) as Label).string = "x" + (this.getNum(node) + 1);
        count--;
        tween(node)
            .to(t / 2, { y: aimY + 10 })
            .to(t / 2, { y: aimY })
            .call(() => {
                this.dojump(node, t, count);
            })
            .start();
    }

    getNum(item: Node): number {
        let str: string = (item.getComponent(Label) as Label).string;
        let num: number = Number(str.split("x")[1]);
        return num;
    }
}

export enum EBGType {
    "normal" = 0,
    "free" = 1,
}
